import React from 'react';
import './App.css';
import {Layout} from 'antd';
import {HeaderApp} from "./components/Header/Header";
import {SiderApp} from "./components/Sider";
import {Content, Footer} from "antd/es/layout/layout";
import {BrowserRouter, HashRouter, Route} from "react-router-dom";
import {CakesApp} from "./components/MainPages/Cakes";
import {BakingApp} from "./components/MainPages/Baking";
import {CandyBarApp} from "./components/MainPages/CandyBar";
import {AboutMeApp} from "./components/MainPages/AboutMe";


function App() {
  return (
      <HashRouter>
          <Layout>
              <SiderApp/>
              <Layout>
                  <HeaderApp/>
                  <Content style={{ margin: '24px 16px 0' }}>
                      <Route path="/cakes" render={() =>
                          <CakesApp/>}/>
                      <Route path="/baking" render={() =>
                          <BakingApp/>}/>
                      <Route path="/candybar" render={() =>
                          <CandyBarApp/>}/>
                      <Route path="/aboutme" render={() =>
                          <AboutMeApp/>}/>
                  </Content>
                  <Footer style={{ textAlign: 'center', marginTop: 30 }}>Cook Website Design ©2020 Created by Malifor</Footer>
              </Layout>
          </Layout>
      </HashRouter>
  );
}

export default App;
